export default defineComponent({
  name: 'Home',
  data () {
    return {
      // queue: {} as platformClient.Models.Queue
      mostrar_alerta: false,
      conversation_id: '',
      lista_conversation_id: '',
      participant_id: '',
      total: 0,
      nOk: 0,
      nError: 0,
      conversation: {} as platformClient.Models.Conversation,
      queue: {} as platformClient.Models.Queue,
      queue_selected: '',
      queues: [
        { key: 'Clientes-Mail', value: 'e598bc2e-e464-46e2-90b2-79c51bae5710' },
        { key: 'Clientes-Mail-Documentacion', value: 'edfb3536-3b3f-4f13-beb2-fcc2d497ec9d' },
        { key: 'Soporte-AdministracionVentas-Mail', value: 'c7e21a46-fe6f-48dc-a1fb-15433d20c46c' },
        { key: 'Soporte-AdmPostVenta-Mail', value: '3292bd26-4149-4863-a429-1e9db5324fb2' },
        { key: 'Ventas-WA-Hogar', value: '61ba9def-28ed-436f-8ec8-3fc5313a3212' },
        { key: 'Clientes-Mail-Finalizar', value: 'b2c2d6f5-5df1-4f60-a966-75d8718cfb77' }
      ]
    }
  },
  methods: {
    async sleep (ms: number) {
      return new Promise(resolve => setTimeout(resolve, ms))
    },

    async moverMultiplesConversationes (): Promise<void> {
      let i = 0
      let n = 0
      var arrayConversationId = this.lista_conversation_id.split('\n')
      this.total = arrayConversationId.length
      console.log('Cola: [' + this.queue_selected + ']')

      for (i = 0; i < arrayConversationId.length; i++) {
        if (arrayConversationId[i].trim() !== '') {
          this.conversation_id = arrayConversationId[i].trim()
          console.log('[' + this.conversation_id + '] inicio')
          n++
          this.conversation = await genesysCloudService.getConversation(this.conversation_id) ?? {}
          console.log('conversation.participants: [' + this.conversation.participants.length + ']')

          await this.moverConversationID(this.conversation, this.queue_selected)
          console.log('espero 1 seg')
          await this.sleep(1000)
          this.conversation_id = ''
          // this.queue_selected = ''
        } else {
          console.log('linea vacia, no se cuenta')
        }
      }
      console.log('total de lineas:' + this.total)
      await this.sleep(1000)
      console.log('total de conversaciones:' + n)
    },
    //
    // async moverInteraccion (queueId: string): Promise<void> {
    async moverInteraccion (): Promise<void> {
      // this.queue = await genesysCloudService.getQueue(this.queue_selected) ?? {}
      // console.log('queue_selected.memberCount: [' + this.queue.memberCount + ']')

      // ACA HAY QUE MOVER LA INTERACCION DE COLA PARA LUEGO AVISAR QUE SE HIZO OK!!!!
      // obtengo la conversacion
      this.conversation = await genesysCloudService.getConversation(this.conversation_id) ?? {}
      console.log('conversation.participants: [' + this.conversation.participants.length + ']')

      // try {
      // no me funcionó el return de la funcion moverConversationID
      const resultado = this.moverConversationID(this.conversation, this.queue_selected)
      // console.log('resultado: ' + resultado)
      this.conversation_id = ''
      this.queue_selected = ''
      // aun no puedo disparar el catch si falla la llamada a la api!!!! como para mostrar un alerta real
      this.mostrar_alerta = true
      // } catch (ex1) {
      //
      // console.log('Excepcion:' + JSON.stringify(ex1))
      // this.mostrar_alerta = false
      // }
    },

    async desconectarMultiplesConversationes (): Promise<void> {
      let i = 0
      let n = 0
      var arrayConversationId = this.lista_conversation_id.split('\n')
      this.total = arrayConversationId.length

      for (i = 0; i < arrayConversationId.length; i++) {
        if (arrayConversationId[i].trim() !== '') {
          this.conversation_id = arrayConversationId[i].trim()
          console.log('[' + this.conversation_id + '] inicio')
          n++
          genesysCloudService.disconnectConversation(this.conversation_id).then((value) => {
            const resultado: string = value
            console.log('[disconnectConversation]resultado: ' + resultado)
            if (resultado === 'OK') {
              this.nOk++
            } else {
              this.nError++
            }
          })
          console.log('espero 1s p/no superar api calls')
          await this.sleep(1000)
          this.conversation_id = ''
        } else {
          console.log('linea vacia, no se cuenta')
          this.nError++
        }
      }
      console.log('total de lineas:' + this.total)
      console.log('total de conversaciones procesadas:' + this.nOk)
      console.log('total de conversaciones con error:' + this.nError)
      this.total = 0
      this.nError = 0
      this.nOk = 0
    },
    // async desconectarInteraccion (conversationId: string): Promise<void> {
    async desconectarInteraccion (): Promise<void> {
      try {
        genesysCloudService.disconnectConversation(this.conversation_id)
      } catch (exDisconnect) {
        //
        console.log('Excepcion:' + JSON.stringify(exDisconnect))
      }
    },

    async eliminarMultiplesContactos (): Promise<void> {
      let i = 0
      let n = 0
      var arrayContactId = this.lista_conversation_id.split('\n')
      this.total = arrayContactId.length

      for (i = 0; i < arrayContactId.length; i++) {
        if (arrayContactId[i].trim() !== '') {
          this.conversation_id = arrayContactId[i].trim()
          console.log('[' + this.conversation_id + '] inicio')
          n++
          genesysCloudCPGService.deleteContact('1756bdd2-35a8-4fb6-bf07-660087504c21', this.conversation_id).then((value) => {
            const resultado: string = value
            console.log('[deleteContact]resultado: ' + resultado)
            if (resultado === 'OK') {
              this.nOk++
            } else {
              this.nError++
            }
          })

          console.log('espero 1s p/no superar api calls')
          await this.sleep(1000)
          this.conversation_id = ''
        } else {
          console.log('linea vacia, no se cuenta')
          this.nError++
        }
      }

      // await this.sleep(500)
      console.log('total de lineas:' + this.total)
      console.log('total de contactos procesados:' + this.nOk)
      console.log('total de contactos con error:' + this.nError)
      this.total = 0
      this.nError = 0
      this.nOk = 0
    },

    async deleteContact (): Promise<void> {
      try {
        genesysCloudCPGService.deleteContact('1756bdd2-35a8-4fb6-bf07-660087504c21', this.conversation_id)
      } catch (exCPG) {
        //
        console.log('Excepcion:' + JSON.stringify(exCPG))
      }
    },

    async moverConversationID (conversation: platformClient.Models.Conversation, queueId: string): Promise<void> {
      // obtengo el participantId del ultimo participant del tipo acd
      // y queueId != null para garantizar que lo puedo sacar de una cola correcta
      // para ello invierto el json de participantes (para agarrar primero al ultimo del tipo acd)
      let analizar = true
      let participantId = ''
      let conversationId = ''
      try {
        conversation.participants
          .slice() // copiar
          .reverse() // invertir el orden en la copia
          .forEach(function (participant) {
          // .forEach(async participant => {
            if (analizar) {
              console.log('participant.purpose: [' + participant.purpose + ']')
              console.log('participant.participantId: [' + participant.id + ']')
              console.log('-----------------')
              if (participant.purpose === 'acd') {
                // tengo el ultimo participant del tipo acd, veo si tiene queueid
                if (participant.queueId != null) {
                  // EUREKA!!!
                  console.log('*** PARTICIPANTE ENCONTRADO ***')
                  console.log('Se mueve ixn: [' + conversation.id + ']')
                  console.log('a la cola id: [' + queueId + ']')
                  console.log('participantId a usar: [' + participant.id + ']')
                  if (participant.id) {
                    participantId = participant.id
                  }
                  if (conversation.id) {
                    conversationId = conversation.id
                  }
                  // *** ACA TENGO QUE INVOCAR LA FUNCION QUE MUEVE LA INTERACCIONES DE EMAIL ***
                  // el await funciona si hago async al forEach
                  // const data = await genesysCloudService.conversationsEmailParticipantReplace(conversationId, participantId, queueId) ?? {}
                  const data = genesysCloudService.conversationsEmailParticipantReplace(conversationId, participantId, queueId) ?? {}
                  analizar = false
                }
              }
            } else {
              console.log('no analizo mas participantes')
            }
          })
      } catch (ex) {
        //
        console.log('Excepcion:' + JSON.stringify(ex))
      }
    }
  }
})
